.AnalyticsContainer {
    display: grid;
    grid-template-rows: 1fr 1fr;
    background-color: #0c0c0c;
    height: auto;
    padding-top: 20px;
  }
  
  .AnalyticsUpperContainer {
    margin-top: 50px;
    display: grid;
    grid-template-columns: 1fr 1.35fr;
  }
  
  .AnalyticsLeft, .AnalyticsNext {
    display: flex;
    height: auto;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .AnalyticsHead {
    margin-left: 22%;
    font-family: Arial, Helvetica, sans-serif;
    letter-spacing: 5px;
    font-weight: 600;
    color: #e0aa14;
  }
  
  .AnalyticsValue {
    font-weight: 300;
    color: white;
  }
  
  .AnalyticsBottomContainer {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .AnalyticsText{
    font-family: 'Verdana';
    color: #b3b3b3;
  }
  .AnalyticsValue{
    font-weight: 500;
    font-size: 45px;
    color: #ffffff;
  }
  .BottomUp {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 70px;
    margin-top: 50px;
  }
  
  @media (max-width: 1200px) {
    .AnalyticsContainer {
      grid-template-rows: 1fr;
    }
  }
  
  @media (max-width: 768px) {
    .AnalyticsUpperContainer {
      grid-template-columns: 1fr;
      margin-top: 30px;
    }
  
    .AnalyticsLeft {
      margin-left: 0;
    }
  
    .AnalyticsHead {
      margin-left: 0;
      font-size: 20px;
    }
  
    .AnalyticsPara {
      font-size: 14px;
    }
  }
  
  @media (max-width: 480px) {
    .AnalyticsUpperContainer {
      grid-template-columns: 1fr;
      margin-top: 20px;
    }
  
    .AnalyticsLeft, .AnalyticsNext {
      height: auto;
    }
  
    .AnalyticsHead {
      margin-left: 32px;
      font-size: 16px;
      font-family: Arial, Helvetica, sans-serif;
    }
  
    .AnalyticsPara {
      font-size: 13px;
      margin-top: 5px;
      margin-left: 35px;
    }
  
    .AnalyticsBottomContainer {
      margin-bottom: 50px;
    }
  
    .BottomUp {
      margin-top: 30px;
      grid-template-columns: 1fr 1fr;
      gap: 30px;
    }
  
    .AnalyticsValue {
      font-size: 45px;
    }
  
    .AnalyticsText {
      font-size: 14px;
      opacity: 0.8;
    }
  }