.all-courses{
    background-color: #0c0c0c;
    height: auto;
    padding-bottom: 100px;
}
.course-back{
    background-image: url('D:\Usman\dw-consultant\public\courses.jpg');
    background-size: cover;
    opacity: 0.8;
    height: 470px;
    display: flex;
    flex-direction: column;
    align-items: center;
    place-content: center;
    margin-bottom: 100px;
    border-radius: 10px;
}
.course-introduction{
    margin: 100px;
    margin-left: 200px;
    margin-right: 200px;
}
.course-para{
    color: white;
}   
.course-title{
    font-size: 55px;
    color: white;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}
.course-sub-title{
    color: white;
}
.course-title-1{
    color: #cca028;
}
.Company1Head{
    color: white;
}
.course-1{
    display: grid;
    grid-template-columns: 300px 1fr;
    margin-left: 200px;
    margin-right: 200px;
}
.course-1left, .course-2, .course-3, .course-4{
    border: 1px solid white;
    display: flex;
    background-image: url('D:\Usman\dw-consultant\public\about-2.jpg');
    background-size: cover;
}
.course-2{
    background-image: url('D:\Usman\dw-consultant\public\leadership.jpg');
}
.course-3{
    background-image: url('D:\Usman\dw-consultant\public\4.jpg');
}
.course-4{
    background-image: url('D:\Usman\dw-consultant\public\contact.jpg');
}
.course-next-inner{
    color: white;
    padding-left: 20px;
}
.course-line{
    color: white;
    background-color: white;
    margin-left: 200px;
    margin-right: 200px;
}
.course-sub-title{
    color: #c2c2c2;
}
.course-date{
    color: #c2c2c2;
    margin-top: -10px;
}
.course-location{
    color: #cca028;
    font-style: italic;
}
.course-disp{
    margin-top: -10px;
    color: #ffffff;
}
.course-readmore{
    margin-top: -8px;
    background-color: transparent;
    color: #ffffff;
    border: 1px solid white;
    text-decoration: underline;
    font-style: italic;
    height: 30px;
    width: 140px;
    display: flex;
    align-items: center;
    place-content: center;
    border-radius: 5px;
}
.my-courses{
    margin-top: 60px;
  }
  @media (max-width: 768px) {
    .course-title {
        font-size: 2.5rem;
    }

    .course-introduction {
        margin: 0 10%;
    }

    .course-1 {
        grid-template-columns: 1fr;
        margin: 0 10%;
    }

    .course-1left {
        height: 350px;
    }

    .course-next-inner {
        padding: 15px;
    }

    .course-readmore {
        height: 25px;
        width: 120px;
    }
}

@media (max-width: 480px) {
    .course-title {
        font-size: 1.8rem;
    }

    .course-introduction {
        margin: 0 5%;
    }

    .course-1 {
        grid-template-columns: 1fr;
        margin: 0 5%;
    }

    .course-1left {
        height: 220px;
    }

    .course-next-inner {
        padding: 10px;
    }

    .course-readmore {
        height: 20px;
        width: 100px;
    }
}