.Contact {
    background-color: #0c0c0c;
    height: auto;
}
.contact-back{
    background-image: url('D:\Usman\dw-consultant\public\0001.jpg');
    background-size: cover;
    opacity: 0.8;
    height: 500px;
    display: flex;
    flex-direction: column;
    align-items: center;
    place-content: center;
}
.contact-rect-outer{
    margin-top: 20px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
}
.rect1, .rect2, .rect3{
    margin-top: 40px;
    transition: 1s all ease;
    opacity: 0;
}
.contact-rect-inner{
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
    place-content: center;
    height: 150px;
    width: 120px;
    background-color: #ca9400;
    margin-left: 10px;
    margin-right: 10px;
    cursor: pointer;
}

.im-contact {
    height: 470px;
    width: 100%;
    object-fit: contain;
    border-bottom: 1px solid rgb(119, 119, 119);
}

.contact-icon-1 {
    display: flex;
    border-radius: 50%;
    align-items: center;
    height: 45px;
    width: 45px;
    place-content: center;
    background-color: #c49514;
    cursor: pointer;
    transition: 0.5s ease;
}

.contact-icon-1:hover{
    transform: rotate(10deg);
}
.contact-icon-name {
    color: #0c0c0c;
}

.contact-icons {
    padding-top: 50px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin-left: 42%;
    margin-right: 42%;
    height: auto;   
}
@media (max-width: 480px) {
    .contact-icons{
        margin-left: 23%;
        margin-right: 23%;
    }
    .im-contact {
        height: 200px;
        width: 100%;
        object-fit: contain;
        border-bottom: 1px solid rgb(119, 119, 119);
    }
    .contact-rect-inner{
        height: 120px;
        width: 100px;
        margin-left: 10px;
        margin-right: 10px;
    }
}
.contact-header {
    padding-top: 25px;
    display: flex;
    flex-direction: column;
    align-items: center;
    place-content: center;
}

.header-sub-text {
    margin-top: -10px;
    color: rgb(128, 128, 128);
    font-size: 13px;
}

.header-text {
    font-size: 14px;
    color: rgb(255, 196, 0);
    font-weight: 500;
}
/*---------------------------------------------Contcat-Content-----------------------------*/
@media (max-width: 720px) {
    .contact-content{
        margin-left: -20px;
        margin-right: -20px;
    }
    .about-para{
        font-size: 12px;
    }
}