.learn-container{
    height: 1000px;
    background-color: #0c0c0c;
    display: flex;
    padding-top: 100px;
    flex-direction: column;
    align-items: center;
}
.learn-bottom{
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-left: 150px;
    margin-right: 150px;
}
.im-learn{
    height: 640px;
    width: 640px;
    object-fit: contain;
}
.learn-left{
    display: flex;
    align-items: center;
    place-content: center;
}