.home-about {
    background-color: #0c0c0c;
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding-bottom: 50px;
}

.home-about-left {
    display: flex;
    flex-direction: column;
    padding-left: 120px;
    margin-left: 80px;
    padding-top: 100px;
}

.home-about-next {
    display: flex;
}

.about-text {
    color: white;
}

.im-home-about {
    width: 420px;
    height: 380px;
    object-fit: cover;
    padding-left: 40px;
    padding-top: 100px;
    margin-top: 10px;
    margin-left: 30px;
}

.home-about-head {
    color: #d6aa32;
    font-weight: 600;
}

.learn-more {
    color: rgb(145, 145, 145);
    font-style: italic;
    text-decoration: underline;
    margin-top: -10px;
}

@media (max-width: 768px) {
    .home-about {
        grid-template-columns: 1fr;
    }

    .home-about-left {
        margin-left: 20px;
        padding-left: 20px;
        padding-right: 20px;
        padding-top: 50px;
    }

    .home-about-next {
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .im-home-about {
        width: 100%;
        max-width: 400px;
        height: auto;
        padding-left: 0;
        padding-right: 0;
        margin: 0;
    }
}

@media (max-width: 480px) {
    .home-about {
        grid-template-columns: 1fr;
    }

    .home-about-left {
        margin: 0;
        padding: 10px;
        padding-left: 30px;
        padding-right: 30px;
    }

    .home-about-next {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: -70px;
    }

    .im-home-about {
        width: 100%;
        max-width: 350px;
        height: auto;
        margin: 0;
        margin-left: 20px;
        margin-right: 20px;
    }
}
