.inspire-container{
    background-color: #0c0c0c;
    height: auto;
    padding-bottom: 100px;
    padding-top: 50px;
    display: flex;
    flex-direction: column;
}
.inspire-left-1{
    display: flex;
}
.inspire-icon-box{
    background-color: #cca028;
    height: 60px;
    margin-left: 20px;
    display: flex;
    align-items: center;
    place-content: center;
    width: 60px;
    border-radius: 50%;
}
.my-line-inspire{
    background-color: #b1b1b1;
    width: 265px;
    margin-right: 200px;
    margin-top: -6px;
}
.inspire-next-inner{
    height: 300px;
    padding: 20px;
    width: 400px;
    background-color: #ffffff;
    border-top: 4px solid #c29009;
    display: flex;
    padding: 10px;
    flex-direction: column;
}
.inspire-next-inner:hover{
    background-color: #ffe9ae;
}
.inspire-next-inner:hover .inspire-button{
    background-color: #e9b544;
    color: #ffffff;
}
.inspire-button{
    width: 120px;
    height: 35px;
    background-color: #e4b536;
    color: #0c0c0c;
    border: none;
    border-radius: 5px;
}
.box-inner-para{
    font-size: 15px;
}
.inspire-bottom{
    margin-top: 50px;
    display: grid;
    grid-template-columns: 1fr 1fr;
}
.inspire-next{
    margin-right: 80px;
    background-image: url('D:\Usman\dw-consultant\public\02.jpg');
    background-size: cover;
    width: 550px;
    display: flex;
    place-content: center;
}
.inspire-box-content{
    margin-left: 10px;
    margin-right: 15px;
}
.inspire-title-para{
    color: white;
    margin-left: 10px;
    font-size: 18px;
}
.inspire-left-boxes{
    background-color: black;
    transition: 0.5s ease;
    margin-top: 5px;
    margin-bottom: 5px;
    display:grid;
    grid-template-columns: 100px 1fr; 
    align-items: center;
    place-content: center;
    padding: 10px;
    margin-left: 100px;
    border-left: 3px solid #494949;
    border-top: 3px solid #494949;
}
.inspire-left-boxes:hover{
    background-color: #cccccc;
    transform: translateX(30px);
    border: 1px solid black;
}
.inspire-left-boxes:hover .box-para{
    color: #0c0c0c;
}
.inspire-left-boxes:hover .inspire-icon-box{
    background-color: #0c0c0c;
}
.inspire-left-boxes:hover .box-icons{
    color: #cca028;
}

.box-title{
    color: #cca028;
}
.box-para{
    font-family: Arial, Helvetica, sans-serif;
    color: white;
    font-size: 14px;
}
.inspire-left{
    margin-top: 30px;
    display: flex;
    align-items: center;
    flex-direction: column;
    height: auto;
    margin-bottom: -30px;
}
.inspire-span-text{
    color: #cca028;
    font-weight: 800;
    border-radius: 50px;
    font-family:Arial, Helvetica, sans-serif;
}
.inspire-title{
    color: #f8f8f8;
    text-align: center;
    font-size: 50px;
    word-spacing: 5px;
}
.inspire-text{
    color: #818181;
    font-family: roboto;
    font-size: 40px;
    margin-bottom: -50px;
    margin-left: 60px;
    opacity: 0.1;
    text-decoration: wavy;
    font-style: italic;
    align-items: center;
    place-self: center;

}
.inspire-up{
    padding-left: 300px;
    padding-right: 300px;
}
.inspire-line{
    color: #f8f8f8;
    background-color: #f8f8f8;
    width: 150px;
}

