.maps-container{
    background-color: #0c0c0c;
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 100px;
}
.about-head-01{
    color: white;
}
.maps-left{
    padding-top: 10px;
    display: flex;
    flex-direction: column;
}
.maps-next{
    display: flex;
    place-content: center;
}
.map-location{
    height: 430px;
    width: 450px;
    margin: 10px;
    color: #0c0c0c;
    background-color: #0c0c0c;
}
.back-color{
    background-color: #0c0c0c;
}
@media (max-width: 480px) {
    .map-location{
        width: 320px;
        height: 300px;
    }
    .maps-container{
        grid-template-columns: 1fr;
        padding-left: 10px;
        padding-right: 10px;
    }
    .maps-left{
        padding-left: 5%;
        padding-right: 5%;
        
    }
}
.more-links{
    display: grid;
    grid-template-columns: 120px 120px 120px;
    width: 100%;
    background-color: #0c0c0c;
    place-content: center;
}
.social-rect{
    border-radius: 5px;
    display: flex;
    height: 120px;
    padding-top: 40px;
    width: 100px;
    background-color: #ca9400;
    align-items: center;
    place-content: center;
    flex-direction: column;
    cursor: pointer;
}
.rect01, .rect02, .rect03{
    transition: 0.5s ease;
}
.rect1:hover .rect01{
    transform: translateY(-14px) rotate(10deg);
}
.rect1:hover .copy-1{
    opacity: 1;
}
.rect2:hover .rect02{
    transform: translateY(-14px) rotate(10deg);
}
.rect2:hover .copy-2{
    opacity: 1;
}
.rect3:hover .rect03{
    transform: translateY(-14px) rotate(10deg);
}
.rect3:hover .copy-3{
    opacity: 1;
}
.copy-info{
    transition: 0.5s ease;
    padding-top: 10px;
    display: flex;
    align-items: center;
    place-content: center;
    gap: 5px;
    opacity: 0;
}
.copy-icon{
    margin-top: -20px;
}
.copy-text{
    font-size: 14px;
}