.about-container {
    background-color: #0c0c0c;
    height: 2000px;
}

.about-rect {
    height: 65px;
    border: 1px solid #0c0c0c;
    width: 180px;
    display: flex;
    align-items: center;
    place-content: center;
    border-radius: 5px;
    cursor: pointer;
}

.about-head {
    font-size: 30px;
    color: #0c0c0c;
}

.about-title {
    font-size: 20px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    color: #d6d6d6;
}
.about-rect-outer{
    margin-top: 20px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
}
.about{
    color: #f7f7f7;
}
.about-rect-inner{
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
    place-content: center;
    height: 150px;
    width: 120px;
    background-color: #ca9400;
    margin-left: 10px;
    margin-right: 10px;
    cursor: pointer;
}
.about-back {
    background-image: url('D:\Usman\dw-consultant\public\02.jpg');
    background-size: cover;
    opacity: 0.8;
    height: 500px;
    display: flex;
    flex-direction: column;
    align-items: center;
    place-content: center;
}
.about-head{
    color: #ffffff;
    font-size: 14px;
    font-style: italic;
    font-weight: 500;
}
.about-main {
    padding-top: 100px;
    padding-left: 200px;
    padding-right: 200px;
    display: grid;
    grid-template-columns: 1fr;
}
.about-left{
    padding-top: 20px;
    display: flex;
    flex-direction: column;
}
.about-left-head{
    color: #e2ab14;
    font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}
.about-left-para{
    color: white;
    text-align: justify;
}
.about-next{
    display: flex;
    align-items: center;
    place-content: center;
}
.im-about-1{
    margin-top: -50px;
    margin-right: 40px;
    height: 450px;
    width: 450px;
    object-fit: contain;
    opacity: 0.6;
}
@media (max-width: 1024px) {
    .about-main {
        padding: 40px 15px;
    }

    .about-back {
        height: 400px;
    }

    .about-rect-inner {
        height: 130px;
        width: 100px;
    }

    .about-left-head {
        font-size: 1.8rem;
    }

    .about-left-para {
        font-size: 0.95rem;
    }
}

@media (max-width: 768px) {
    .about-back {
        height: 350px;
        padding: 0 10px;
    }

    .about {
        font-size: 2rem;
    }

    .about-title {
        font-size: 1.2rem;
    }

    .about-rect-outer {
        align-items: center;
    }

    .about-rect-inner {
        height: 120px;
        width: 100px;
    }

    .about-left {
        padding: 0 10px;
    }

    .about-left-head {
        font-size: 1.6rem;
    }

    .about-left-para {
        font-size: 0.9rem;
    }
}

@media (max-width: 480px) {
    .about-back {
        height: 300px;
        padding: 0 5px;
    }

    .about {
        font-size: 1.8rem;
    }

    .about-title {
        font-size: 1rem;
    }

    .about-rect-inner {
        height: 100px;
        width: 80px;
    }

    .about-left-head {
        font-size: 1.4rem;
    }

    .about-left-para {
        font-size: 0.85rem;
    }
}