.popup-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.6);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
  }
  
  .popup-container {
    position: relative;
    background-color: rgb(255, 255, 255);
    padding: 30px;
    border-radius: 10px;
    width: 400px;
    text-align: center;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.2);
    animation: slideDown 0.3s ease-in-out;
  }
  
  .close-button {
    position: absolute;
    top: 5px;
    right: 5px;
    background: none;
    border: none;
    font-size: 20px;
    cursor: pointer;
  }
  
  .popup-title {
    font-size: 24px;
    margin-bottom: 20px;
    color: #333;
    font-weight: bold;
  }
  
  .email-container {
    position: relative;
    display: flex;
    align-items: center;
    margin-bottom: 20px;
  }
  
  .email-icon {
    position: absolute;
    left: 10px;
    color: #333;
    font-size: 20px;
  }
  
  .email-input {
    width: 100%;
    padding: 12px 12px 12px 40px;
    border-radius: 25px;
    border: 1px solid #ccc;
    font-size: 16px;
    outline: none;
    box-sizing: border-box;
  }
  
  .subscribe-button {
    background-color: #f4b400;
    color: white;
    border: none;
    padding: 12px 20px;
    border-radius: 25px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .subscribe-button:hover {
    background-color: #e39e00;
  }
  
  @keyframes slideDown {
    from {
      transform: translateY(-50%);
      opacity: 0;
    }
    to {
      transform: translateY(0);
      opacity: 1;
    }
  }
  