.business-container{
    height: auto;
    background-color: #0c0c0c;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-left: 150px;
    padding-right: 150px;
    padding-top: 150px;
    padding-bottom: 150px;
}

.business-bottom{
    margin-top: 50px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;   
    gap: 20px;
    opacity: 1;
}
.card-1, .card-2, .card-3{
    border-top: 6px solid rgb(255, 187, 0);
    display: flex;
    flex-direction: column;
    height: 400px;
    width: 350px;
}

.card-top{
    height: 280px;
}
.card-1{
    background-image: url('D:\Usman\dw-consultant\public\card1.jpg');
    background-size: cover;
}
