.business-slider{
    margin-top: -7px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;  
    position: relative;
}
.card-bottom{
    text-align: center;
    background-color: #000000;
    padding: 10px;
    opacity: 0.7;
    height: 200px;
    align-self: stretch;
}
.card-head{
    color: rgb(255, 187, 0);
}
.card-text{
    color: white;
}
.bold-1{
    font-weight: 700;
}
.bold{
    color: rgb(255, 187, 0);;
}
.head{
    color: white;
    font-weight: 700;
}
.para{
    color: white;
    text-align: center;
    margin-top: 10px;
    font-size: 18px;
}
.left-Icon, .next-Icon{
    height: 35px;
    width: 35px;
    color:rgb(124, 124, 124);
    transition: 0.2s ease;
}
.left-Icon:hover{
    color: rgb(224, 224, 224);
}
.next-Icon:hover{
    color: rgb(224, 224, 224);
}
.business-quotes{
    color: white;
}
.quote{
    color: rgb(255, 255, 255);
    font-size: 18px;
    font-weight: 600;
    font-family: Arial, Helvetica, sans-serif;
}