.disclamer-container{
    height: 1000px;
    background-color: #0c0c0c;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 130px;
}
.disclamer-cards{
    padding-top: 30px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 30px;
    margin-left: 150px;
    margin-right: 150px;
}
.card-01, .card-02, .card-03{
    padding: 20px;
    margin-top: 10px;
    border-radius: 12px;
    background-color: rgb(0, 0, 0);
    border-left: 3px solid #ffffff;
    transition: 0.2s ease;
    cursor: pointer;
}
.card-head-dis{
    padding-top: 10px;
    color: #caa729;
    font-weight: 600;
}
.read-more-disp:hover{
    color: white;
}
.read-more-disp{
    margin-top: -10px;
    cursor: pointer;
    color: rgb(139, 139, 139);
    font-style: italic;
    text-decoration: underline;
}
.disp-line{
    margin-top: 10px;
    background-color: #cea40d;
}
.card-01:hover{
    color: white;
    background-color: #000000;
    border-left: 3px solid #ffc400;
    transform: scale(1.01);
}
.card-02:hover{
    color: white;
    background-color: #000000;
    border-left: 3px solid #ffc400;
    transform: scale(1.02);
}
.card-03:hover{
    color: white;
    background-color: #000000;
    border-left: 3px solid #ffc400;
    transform: scale(1.02);
}
.card-01:hover{
    color: white;
    background-color: #000000;
    border-left: 3px solid #ffc400;
    transform: scale(1.01);
}
.card-02:hover{
    color: white;
    background-color: #000000;
    border-left: 3px solid #ffc400;
    transform: scale(1.02);
}
.dis-icon{
    color: white;
    height: 30px;
    width: 30px;
}
.card-03:hover .dis-icon{
    color: #caa729;
}
.card-01:hover .dis-icon{
    color: #caa729;
}
.card-02:hover .dis-icon{
    color: #caa729;
}
.head01{
    font-size: 45px;
    color: white;
    font-family: 800;
}
.head-disp{
    font-size: 40px;
}
.para-dis{
    font-size: 18px;
    margin-top: 5px;
    color: white;
}
.dis-para{
    font-size: 18px;
    margin-top: 30px;
    color: white;
}
.para-disp-inner{
    color: white;
    font-family: Arial, Helvetica, sans-serif;
}