.offer-container {
    background-color: #0c0c0c;
    display: flex;
    flex-direction: column;
    padding: 150px;
    padding-top: 100px;
    padding-bottom: 0;
}

.im-offer {
    height: 180px;
    width: 100%;
    object-fit: cover;
    border-radius: 5px; 
}

.offer-name {
    padding-left: 10px;
    padding-top: 10px;
    color: rgb(134, 134, 134);
}

.offer-para {
    margin-top: 5px;
    color: rgb(255, 255, 255);
    text-align: justify;
}

.offer-title {
    margin-bottom: 10px;
    color: #cc9f25;
}

.our-offers {
    display: grid;
    grid-template-columns: repeat(4, 1fr); 
    gap: 10px; 
    margin-top: 30px;
}

.icon-offer-1 {
    margin-top: 3px;
}

.offers {
    height: 340px;
    width: 250px;
    background-color: #000000;
    border-radius: 2px;
    display: flex;
    flex-direction: column;
    border: 1px solid rgb(172, 172, 172);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    overflow: hidden; 
    align-items: center;
    border-radius: 4px;
    cursor: pointer;
    transition: 0.5s all ease;
}

.offers:hover {
    transform: scale(1.05);
}

.offer-line {
    margin-top: 10px;
    color: #ececec;
    padding-left: 10px;
}

.offers-duration {
    margin-top: 10px;
    display: flex;
    gap: 20px;
}

.offer-duration-1 {
    display: flex;
    padding-left: 10px;
    margin-top: 10px;
    gap: 5px;
}

.offer-duration-2 {
    display: flex;
    margin-top: 10px;
    padding-left: 50px;
    gap: 5px;
}

.lessons {
    color: #c2c2c2;
    font-size: 13px;
}

@media (max-width: 768px) {
    .offer-container {
        padding-top: 100px;
    }

    .our-offers {
        grid-template-columns: repeat(2, 1fr);
        gap: 15px;
    }

    .offers {
        height: 300px;
        width: 100%;
    }

    .im-offer {
        height: 140px;
    }

    .offer-duration-2 {
        padding-left: 20px;
    }
}

@media (max-width: 480px) {
    .offer-container {
        padding: 50px 10px;
        padding-top: 200px;
    }

    .our-offers {
        grid-template-columns: 1fr;
        gap: 40px;
    }

    .offers {
        height: auto;
        width: 100%;
    }

    .im-offer {
        height: 120px;
    }

    .offer-name {
        padding-left: 5px;
        padding-top: 5px;
    }

    .offer-duration-2 {
        padding-left: 10px;
    }
    .offer-title{
        font-size: 34px;
        margin-left: 10px;
        margin-right: 10px;
    }
    .offer-para{
        font-size: 15px;
        margin-left: 10px;
        margin-right: 10px;
    }
    .im-offer{
        height: 200px;
    }

    .lessons {
        font-size: 12px;
    }
}
